.input-group-text {
  padding: $input-padding-y 0.625rem;
  font-size: $font-size-base;
  color: $medium-gray;

  .material-icons {
    font-size: $font-size-base;
  }

  // collapse borders
  & + .input-group-text {
    margin-left: $input-border-width * -1;
  }
}



